<template>
    <div class="widget1">
            <div class="grid">
            <div class="left-panel panel">
                <div class="date">
                    {{date}}
                </div>
                <div class="temp">
                   <img v-if="icon == 'clear-day'" src="@/assets/weather-icons/line/clear-day.svg" alt="" width="60">
                   <img v-else-if="icon == 'clear-night'" src="@/assets/weather-icons/line/clear-night.svg" alt="" width="60">
                   <img v-else-if="icon == 'cloudy'" src="@/assets/weather-icons/line/cloudy.svg" alt="" width="60">
                   <img v-else-if="icon == 'fog'" src="@/assets/weather-icons/line/fog.svg" alt="" width="60">
                   <img v-else-if="icon == 'partly-cloudy-day'" src="@/assets/weather-icons/line/partly-cloudy-day.svg" alt="" width="60">
                   <img v-else-if="icon == 'partly-cloudy-night'" src="@/assets/weather-icons/line/partly-cloudy-night.svg" alt="" width="60">
                   <img v-else-if="icon == 'rain'" src="@/assets/weather-icons/line/rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'rain-snow'" src="@/assets/weather-icons/line/snowflake.svg" alt="" width="60">
                   <img v-else-if="icon == 'rain-snow-showers-day'" src="@/assets/weather-icons/line/snowflake.svg" alt="" width="60">
                   <img v-else-if="icon == 'rain-snow-showers-night'" src="@/assets/weather-icons/line/snowflake.svg" alt="" width="60">
                   <img v-else-if="icon == 'showers-day'" src="@/assets/weather-icons/line/partly-cloudy-day-rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'showers-night'" src="@/assets/weather-icons/line/partly-cloudy-night-rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'sleet'" src="@/assets/weather-icons/line/sleet.svg" alt="" width="60">
                   <img v-else-if="icon == 'snow'" src="@/assets/weather-icons/line/snow.svg" alt="" width="60">
                   <img v-else-if="icon == 'snow-showers-day'" src="@/assets/weather-icons/line/partly-cloudy-day-snow.svg" alt="" width="60">
                   <img v-else-if="icon == 'snow-showers-night'" src="@/assets/weather-icons/line/partly-cloudy-night-snow.svg" alt="" width="60">
                   <img v-else-if="icon == 'thunder'" src="@/assets/weather-icons/line/thunderstorms.svg" alt="" width="60">
                   <img v-else-if="icon == 'thunder-rain'" src="@/assets/weather-icons/line/thunderstorms-rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'thunder-showers-day'" src="@/assets/weather-icons/line/thunderstorms-day-rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'thunder-showers-night'" src="@/assets/weather-icons/line/thunderstorms-night-rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'wind'" src="@/assets/weather-icons/line/wind.svg" alt="" width="60">
                   <img v-else src="@/assets/weather-icons/line/not-available.svg" alt="" width="60">
                   {{temp}}&deg;
                </div>
            </div>
            <div class="right-panel panel">
                <div class="desc">
                     {{desc}}
                </div>
                
            </div>
            </div>
        </div>
</template>

<script>
export default {
    name:"dayWeatherCard",
    props:["temp", "date", "desc", "icon"]
}
</script>


<style scoped>

div.widget1{
    position: relative;
    background-color: #fcfdfd;
    border-radius: 9px;
    display: flex;
    flex-direction: row;
    padding: 23px;
    max-width: 600px;
    box-shadow: 0px 31px 45px -26px #080c21;
}

div.left-panel
{
    text-align: left;
}

div.date
{
  font-size: 14px;
  font-weight: bold;
  color: rgba(0,0,0,0.5);
}

div.temp
{
  font-size: 81px;
  color: rgba(0,0,0,0.9);
  font-weight: 100;
}

.desc{
    font-family: 'Lato', sans-serif;
    color: rgba(0,0,0,0.9);
    display: flex;
}

.fas{
    margin-top: 5px;
    margin-right:2px;
}

div.panel
{
  display: inline-block;
}

div.right-panel
{
    width: 300px;
    top: 0;
    margin-top: 90px;
    right: 0;
}

.link{
    position: relative;
    right: 30px;
    padding-left: 270px;
    padding-top: 10px;
    font-size: 30px;
    color: rgba(0,0,0,0.9);
}

@media only screen and (min-width:540px){
    .right-panel{
        position: absolute;
    }

}
</style>