<template>
  <div>
   
    <dashboardNav @nav-update-data="updateData" @update-location="updateData"/>
    <div class="container">

    <h2 class="head">Next 30 Days</h2>
    </div>
    <div aria-busy="true" v-if="loading" class="load"></div>
    <div v-else class="container" v-for="(details) in next15" :key="details.datetimeEpoch">
      <dayWeatherCard :date="details.datetime" :temp="details.temp" :desc="details.conditions" :icon="details.icon"/><br/>
    </div>

  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import mainIndex from "@/components/main.vue"
import currentWeatherCard from "@/components/currentWeatherCard.vue"
import recentWeatherCard from "@/components/recentWeatherCard.vue"
import detailWeatherCard from "@/components/detailWeatherCard.vue"
import hourlyWeatherCard from "@/components/hourlyWeatherCard.vue"
import sunCard from "@/components/sunCard.vue"
import dayWeatherCard from "@/components/dayWeatherCard.vue"
import BarChart from "@/components/chart.vue"
import Settings from "@/components/settings.vue"
import dashboardNav from "@/components/dashboardNav.vue"
import hubble from "../hubble"
export default {
  name: 'Next30ForecastView',
  data(){
    return{
        next15forecast:[],
        next15:[],
        longitude:0,
        latitude:0,
        loading:false
    }
  },
  components: {
    HelloWorld,
    mainIndex,
    currentWeatherCard,
    recentWeatherCard,
    detailWeatherCard,
    hourlyWeatherCard,
    sunCard,
    dayWeatherCard,
    BarChart,
    Settings,
    dashboardNav
  },
  methods:{
    async updateData(){
      const location = this.$store.state.search
      if(location){
        this.loading = true
        this.next15forecast = await hubble.getNext30Weather(location, this.$store.state.unit)
        this.$store.commit('resolveAddress', this.next15forecast[0].resolvedAddress)
        this.next15 = this.next15forecast[0].days
      }else{
        this.loading = true
        const location = await hubble.getLocation()
        this.longitude = location.geoplugin_longitude
        this.latitude = location.geoplugin_latitude
        this.next15forecast = await hubble.getNext30Weather(`${this.longitude},${this.latitude}`, this.$store.state.unit)
        this.$store.commit('resolveAddress', this.next15forecast[0].resolvedAddress)
        this.next15 = this.next15forecast[0].days
        
      }
    }
  },
  async created(){
    const location = this.$store.state.search
    if(location){
      this.loading = true
      this.next15forecast = await hubble.getNext30Weather(location, this.$store.state.unit)
      this.$store.commit('resolveAddress', this.next15forecast[0].resolvedAddress)
      this.next15 = this.next15forecast[0].days
      this.loading=false
    }else{
      this.loading = true
      const location = await hubble.getLocation()
      this.longitude = location.geoplugin_longitude
      this.latitude = location.geoplugin_latitude
      this.next15forecast = await hubble.getNext30Weather(`${this.longitude},${this.latitude}`, this.$store.state.unit)
      this.$store.commit('resolveAddress', this.next15forecast[0].resolvedAddress)
      this.next15 = this.next15forecast[0].days
      this.loading=false
      
    }
  }
}
</script>

<style scoped>
.head{
  text-align: left;
  color: rgba(0,0,0,0.9);
}
</style>

